<template>
	<view>
		<!-- ‰ΩøÁî®ÂéüÁîübuttonÂíåviewÁ°Æ‰øù‰∫ã‰ª∂ËÉΩËß¶Âèë -->
		<view 
			class="batch-selector-button" 
			:class="['btn-' + buttonType, 'btn-' + buttonSize]"
			@tap="showDialog"
		>
			<text v-if="loading">Âä†ËΩΩ‰∏≠...</text>
			<text v-else>{{ buttonText }}</text>
		</view>
		
		<!-- Â§áÁî®ÔºöuViewÊåâÈíÆ -->
		<!-- <u-button 
			:type="buttonType" 
			:text="buttonText" 
			:size="buttonSize"
			@click="showDialog"
			:loading="loading"
		></u-button> -->
		
		<u-popup 
			:show="visible" 
			mode="center"
			:round="20"
			width="90%"
			height="65%"
			:closeable="true"
			:overlay="true"
			:closeOnClickOverlay="true"
			:zIndex="9999"
			:overlayStyle="{ backgroundColor: 'rgba(0, 0, 0, 0.5)' }"
			@close="handleClose"
		>
			<view class="batch-selector-popup">
				<!-- Â§¥ÈÉ® -->
				<view class="popup-header">
					<text class="popup-title">ÈÄâÊã©ÊâπÊ¨°</text>
					<view class="close-btn" @tap="handleClose">
						<text class="close-icon">‚úï</text>
					</view>
				</view>
				
				<!-- ËçØÊùê‰ø°ÊÅØ -->
				<view class="drug-info-bar" v-if="drugInfo">
					<view class="info-row">
						<text class="info-label">ËçØÊùêÔºö</text>
						<text class="info-value">{{ drugInfo.name }}</text>
					</view>
					<view class="info-row">
						<text class="info-label">ËßÑÊ†ºÔºö</text>
						<text class="info-value">{{ drugInfo.specification || drugInfo.spec }}</text>
					</view>
				</view>
				
				<!-- FIFOÊèêÁ§∫ -->
				<view v-if="enableFIFO" class="fifo-tip">
					<text class="tip-icon">üí°</text>
					<text class="tip-text">Â∑≤ÊåâÂÖàËøõÂÖàÂá∫(FIFO)ÊéíÂ∫èÔºå‰ºòÂÖà‰ΩøÁî®ÊúÄÊó©ÊâπÊ¨°</text>
				</view>
				
				<!-- ÊâπÊ¨°ÂàóË°® -->
				<scroll-view class="batch-list" scroll-y>
					<view 
						v-for="batch in batchList" 
						:key="batch._id" 
						class="batch-item"
						@tap="selectBatch(batch)"
					>
						<!-- ËøëÊïàÊúüÊ†áËÆ∞ -->
						<view v-if="batch.isNearExpiry" class="expiry-badge">
							<text>‚ö†Ô∏è ËøëÊïàÊúü</text>
						</view>
						
						<view class="batch-main">
							<view class="batch-header">
								<text class="batch-number">ÊâπÂè∑Ôºö{{ batch.batch }}</text>
								<text class="stock-quantity" :class="{'low-stock': batch.quantity < 10}">
									Â∫ìÂ≠òÔºö{{ batch.quantity }} {{ drugInfo.unit }}
								</text>
							</view>
							
							<view class="batch-details">
								<view class="detail-row">
									<text class="detail-label">Áîü‰∫ßÊó•ÊúüÔºö</text>
									<text class="detail-value">{{ batch.productionDate }}</text>
								</view>
								<view class="detail-row">
									<text class="detail-label">ÊúâÊïàÊúüËá≥Ôºö</text>
									<text class="detail-value" :class="{'text-warning': batch.isNearExpiry}">
										{{ batch.expireDate }}
									</text>
								</view>
								<view class="detail-row" v-if="batch.price">
									<text class="detail-label">Âçï‰ª∑Ôºö</text>
									<text class="detail-value">¬•{{ batch.price }}</text>
								</view>
								<view class="detail-row" v-if="showLocationFilter">
									<text class="detail-label">Â≠òÊîæ‰ΩçÁΩÆÔºö</text>
									<text class="detail-value">{{ getLocationName(batch.location) }}</text>
								</view>
							</view>
						</view>
						
						<!-- Êé®ËçêÊ†áËÆ∞ -->
						<view v-if="enableFIFO && batchList.indexOf(batch) === 0" class="recommend-badge">
							<text>Êé®Ëçê</text>
						</view>
					</view>
					
					<!-- Âä†ËΩΩÁä∂ÊÄÅ -->
					<view v-if="loading" class="loading-hint">
						<text>Âä†ËΩΩ‰∏≠...</text>
					</view>
					
					<!-- Á©∫Áä∂ÊÄÅ -->
					<view v-if="!loading && batchList.length === 0" class="empty-hint">
						<text>üì¶</text>
						<text>ËØ•ËçØÊùêÊöÇÊó†Â∫ìÂ≠ò</text>
						<text class="empty-tip">ËØ∑ÂÖàËøõË°åÂÖ•Â∫ìÊìç‰Ωú</text>
					</view>
				</scroll-view>
			</view>
		</u-popup>
	</view>
</template>

<script>
export default {
	name: 'BatchSelector',
	props: {
		// ÊåâÈíÆÊñáÂ≠ó
		buttonText: {
			type: String,
			default: 'ÈÄâÊã©ÊâπÊ¨°'
		},
		// ÊåâÈíÆÁ±ªÂûã
		buttonType: {
			type: String,
			default: 'info'
		},
		// ÊåâÈíÆÂ∞∫ÂØ∏
		buttonSize: {
			type: String,
			default: 'default'
		},
		// ËçØÊùêID
		drugId: {
			type: String,
			required: true
		},
		// ËçØÊùê‰ø°ÊÅØ
		drugInfo: {
			type: Object,
			default: () => ({})
		},
		// ÈªòËÆ§Âõ≠Âå∫
		defaultLocation: {
			type: String,
			default: ''
		},
		// ÊòØÂê¶ÊòæÁ§∫Âõ≠Âå∫ËøáÊª§
		showLocationFilter: {
			type: Boolean,
			default: true
		},
		// ÊòØÂê¶ÂêØÁî®FIFOÔºàÂÖàËøõÂÖàÂá∫Ôºâ
		enableFIFO: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			visible: false,
			batchList: [],
			loading: false
		}
	},
	methods: {
		showDialog() {
			console.log('=== ÊâπÊ¨°ÈÄâÊã©Âô® showDialog Ë¢´Ë∞ÉÁî® ===')
			console.log('this.drugId:', this.drugId)
			console.log('this.drugInfo:', this.drugInfo)
			console.log('ÊâÄÊúâ props:', {
				drugId: this.drugId,
				drugInfo: this.drugInfo,
				defaultLocation: this.defaultLocation,
				enableFIFO: this.enableFIFO
			})
			
			if (!this.drugId) {
				console.error('‚ùå drugId ‰∏∫Á©∫ÔºÅÊó†Ê≥ïÂä†ËΩΩÊâπÊ¨°')
				uni.showToast({
					title: 'ËØ∑ÂÖàÈÄâÊã©ËçØÊùê',
					icon: 'none'
				})
				return
			}
			
			console.log('‚úÖ drugId Â≠òÂú®ÔºåÊâìÂºÄÊâπÊ¨°ÈÄâÊã©Âô®')
			this.visible = true
			this.batchList = []
			this.loadBatches()
		},
		
		handleClose() {
			this.visible = false
		},
		
		async loadBatches() {
			this.loading = true
			
			// Ë∞ÉËØïÊó•Âøó
			console.log('=== ÊâπÊ¨°ÈÄâÊã©Âô®Ë∞ÉËØï ===')
			console.log('ËçØÊùêID (drugId):', this.drugId)
			console.log('ËçØÊùê‰ø°ÊÅØ (drugInfo):', this.drugInfo)
			console.log('ËçØÊùêÂêçÁß∞:', this.drugInfo?.name)
			console.log('ÈªòËÆ§Âõ≠Âå∫ (location):', this.defaultLocation)
			console.log('ÂêØÁî®FIFO:', this.enableFIFO)
			
			try {
				const result = await wx.cloud.callFunction({
					name: 'stockManage',
					data: {
						action: 'getBatchList',
						data: {
							drugId: this.drugId,
							location: this.defaultLocation || undefined,
							enableFIFO: this.enableFIFO
						}
					}
				})
				
				console.log('‰∫ëÂáΩÊï∞ËøîÂõû:', result.result)
				console.log('ÊâπÊ¨°Êï∞Èáè:', result.result.data?.length || 0)
				
				if (result.result.success) {
					this.batchList = result.result.data
					
					if (this.batchList.length === 0) {
						console.warn('‚ö†Ô∏è ËØ•ËçØÊùêÊöÇÊó†Â∫ìÂ≠òÔºå‰ΩÜÈÄâÊã©È°µÈù¢ÊòæÁ§∫ÊúâÂ∫ìÂ≠ò')
						console.warn('ÂèØËÉΩÂéüÂõ†Ôºö')
						console.warn('  1. drugId ‰∏çÂåπÈÖç')
						console.warn('  2. Âõ≠Âå∫Á≠õÈÄâÂØºËá¥Êó†Â∫ìÂ≠ò')
						console.warn('  3. Â∫ìÂ≠òÂ∑≤Ë¢´ÂÖ∂‰ªñÊìç‰ΩúÊ∂àËÄó')
					} else {
						console.log('‚úÖ ÊâæÂà∞ÊâπÊ¨°:', this.batchList)
					}
				} else {
					throw new Error(result.result.message || 'Êü•ËØ¢Â§±Ë¥•')
				}
			} catch (err) {
				console.error('‚ùå Âä†ËΩΩÊâπÊ¨°Â§±Ë¥•:', err)
				uni.showToast({
					title: err.message || 'Âä†ËΩΩÂ§±Ë¥•',
					icon: 'none'
				})
			} finally {
				this.loading = false
			}
		},
		
		selectBatch(batch) {
			// Ê£ÄÊü•Â∫ìÂ≠ò
			if (batch.quantity <= 0) {
				uni.showToast({
					title: 'ËØ•ÊâπÊ¨°Â∑≤Êó†Â∫ìÂ≠ò',
					icon: 'none'
				})
				return
			}
			
			// ËøëÊïàÊúüÊèêÁ§∫
			if (batch.isNearExpiry) {
				uni.showModal({
					title: 'ËøëÊïàÊúüÊèêÁ§∫',
					content: `ËØ•ÊâπÊ¨°Â∞Ü‰∫é ${batch.expireDate} Âà∞ÊúüÔºåÊòØÂê¶ÁªßÁª≠‰ΩøÁî®Ôºü`,
					success: (res) => {
						if (res.confirm) {
							this.confirmSelect(batch)
						}
					}
				})
			} else {
				this.confirmSelect(batch)
			}
		},
		
		confirmSelect(batch) {
			this.$emit('select', batch)
			this.handleClose()
			
			uni.showToast({
				title: 'Â∑≤ÈÄâÊã©',
				icon: 'success',
				duration: 1000
			})
		},
		
		getLocationName(location) {
			const locationMap = {
				'land_park': 'ÈôÜÂõ≠',
				'water_park': 'Ê∞¥Âõ≠'
			}
			return locationMap[location] || location
		}
	}
}
</script>

